@media (max-height: 610px) {



    
    .intro-slider{
        &__content{
            // padding-bottom: 90px;
            height: 100%;
        }
        &__content-slider{
            height: auto;
        }
        &__wrapper{
            display: block;
        }
    }
    .section-1,
    .section-2,
    .section-3,
    .section-4,
    .section-5,
    .section-6 {
        // height: calc(100vh - 50px);
        height: 100%;
    }
    .section-6 {
        height: auto;

        .intro-slider{
            &__wrapper{
                &-right{
                    height: calc(100vh - 4px);
                }
            }
        }
    }
    .toup{
        &.show{
            bottom: 90px !important;
            right: 15px;
        }
    }
    .section-1{
        display: flex;
        min-height: 600px;
        .intro-slider{
            &__list{
                flex-direction: column;
                row-gap: 30px;
            }
            &__descr{
                margin-bottom: 30px !important;
            }
            &-item{
                &-container{
                    .intro-slider-link  {
                        padding-top: 10px !important;
                        padding-bottom: 10px !important;
                    }
                }
            }
            &__content{
                height: 100%;
                padding-top: 150px;
            }
            
        }
        &-container {
            padding-right: 50px !important;
        }
    }
    .section-2{

        &__content-1 {
            // padding-top: 60px;
            padding-top: 160px;
            padding-right: 50px !important;
        }
        .intro-slider__wrapper-right {
            height: auto;
        }
        .intro-slider__wrapper-left{
            padding-bottom: 30px;
        }
    }
    .section-3{
        min-height: 800px;
        display: flex;
        justify-content: center;
        align-items: center;
        
        &-container {
            // padding: 120px 50px 100px 50px !important;
        }
        &-wrapper {
            margin-bottom: 0px !important;   
        }
        .intro-slider__content {
            margin: 0;
        }
    }
    .section-4{

        &__wrapper-left {
            padding: 0 100px 0 50px !important;
        }
    }
    .section-5{
        min-height: 600px;
        display: flex;
        &-content {
            margin-bottom: 30px !important;
            padding-top: 150px;
        }
        &-container {
            padding: 0px 50px !important;
        }
    }
    .section-6{
        &-container {
            padding: 50px 50px !important;
        }
        .footer{
            &__copyright{
                padding-left: 50px;
            }
        }
        .intro-slider{
            &__wrapper{
                &-left,
                &-right{
                    height: 100vh;
                }
            }
        }
    }
    .header {
        &-container {
            
            padding: 0 90px 0 50px !important;
        }
        .aside-btn {
            right: 30px !important;
            &::after {
                display: none;
            }
        }
    }
    .swiper-scrollbar{
        display: none !important;
    }


    .title::after,
	.section-1 .intro-slider__descr::after {
		top: -25px ;
	}







    .section-3{
        &-wrapper {
            margin-bottom: 15px !important;
        }
        &__title,
        &__items  {
            margin-bottom: 15px !important;
        }
        &__item {
            p{
                font-size: 12px;
                margin-bottom: 8px !important;
            }
            &-title {
                font-size: 15px;
            }
        }
    }
}



@media (max-width: 980px) {
    .section-2{
        .intro-slider{
            &__wrapper{
                &-right{
                    width: 100%;
                }
                &-left{
                    display: none;
                }
            }
        }
        &__content-2{
            &__content-1{
                padding-bottom: 60px !important;
                &__descr {
                    font-size:var(--fz-m) !important;
                }
                &__descr:last-child{
                    margin-bottom: 0 !important;
                }
            }
        }
        .slick-dots{
            list-style: none;
            margin: 0;
            padding: 0;
            position: absolute;
            bottom: 20px;
            left: 45px;
            display: flex;
            column-gap: 10px;
            li{
                width: 28px;
                border-bottom: 4px solid transparent;
                border-top: 4px solid transparent;
                border-color: transparent;
                border-style: solid;
                border-width: 12px 4px;
                background-clip: padding-box;
                height: 27px;
                border-radius: 0;
                background-color: hsla(0,0%,100%,.3);
                cursor: pointer;
                
                &.slick-active{
                    background-color: var(--c-primary);
                }
                button{
                    display: none;
                }
            }
        }
        &__content-1{
            padding-bottom: 150px;

            &__descr:last-child{
                margin-bottom: 0;
            }
        }
        .circle-right{
            display: flex;
        }
        
    }
    .section-4{
        &-title {
            margin-bottom: 20px;
            br{
                display: none;
            }
        }
        &-descr{
            margin-bottom: 0;
        }
        &__wrapper {
            flex-direction: column-reverse;

            
            &-right{
                width: 100%;
                height: 70%;
            }
            &-left{
                width: 100%;
                height: 30%;
                &-container {
                    flex-direction: row;
                    align-items: center;
                    justify-content: space-around;
                    column-gap: 40px;
                }
                &-text{

                }
            }
        }
        .swiper-button-container{
            position: static;

        }
        .swiper-button{
            &-prev,
            &-next{
                width: 60px;
                height: 60px;
            }
        }
    }
    .section-6{

        .intro-slider{
            &__wrapper{
                flex-direction: column;

                &-left,
                &-right{
                    width: 100%;
                }
                &-left {
                    height: 80%;
                }
                &-right{
                    // height: 20%;
                    height: 400px;
                }
            }
        }
        &-container {
            padding: 50px 100px 50px 50px !important;
        }
        .footer{
            flex-shrink: 0;
            &__copyright{
                margin-top: 0px;
            }
        }
        
    }

}

